  border-radius: 0.625rem;
  box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2);
  height: 31.25rem;
  width: 100%;
  overflow: hidden;
  position: relative;
  object-fit: cover;
}
.urban-living .image-container .image-left img,
.urban-living .image-container .image-right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.5s linear;
}
.urban-living .image-container .image-left {
  width: 90%;
}
.urban-living .image-container .image-right {
  width: 45%;
}
.urban-living .image-container .image-container:hover {
  pointer-events: none;
}
.urban-living .image-container .image-right:hover {
  width: 170%;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.8s ease-in-out;
  pointer-events: auto;
}
.urban-living .image-container .image-right:hover ~ .image-left {
