/* Timeline Container */
#timeline {
  display: flex;
  height: 24.375rem;
  background-color: #000000;
}

/* Timeline Item */
.tl-item {
  position: relative;
  width: 10%;
  transition: width 0.5s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #010101;
  border: 1px solid hsla(0deg, 0%, 100%, 0.14);
  margin-left: -0.125rem;
  border-radius: 60px;
}

/* Year Display - Rotated Vertically */
.tl-year {
  background: rgba(0, 0, 0, 0.7);
  padding: 10px 20px;
  border-radius: 10px;
  transition: opacity 0.5s ease-in-out;
  position: absolute;
  z-index: 2;
  transform: rotate(-90deg); /* Rotate the text vertically */
  transform-origin: left top; /* Set rotation origin */
  white-space: nowrap; /* Prevent text from wrapping */
  top: 100%; /* Position vertically */
  left: 0.625rem; /* Adjust horizontal position */
}
.tl-year h2 {
  font-size: 1rem;
  font-weight: var(--font-r);
  color: #FEFEFE;
  line-height: 1.3125rem;
}

/* Hide the year for the first item by default */
.tl-item:first-child .tl-year {
  opacity: 0;
}

/* Show the first item's year when any other item is hovered */
.tl-item:hover ~ .tl-item:first-child .tl-year,
#timeline:hover .tl-item:first-child .tl-year {
  opacity: 1;
}

/* Background Image */
.tl-bg {
  position: absolute;
  width: 100%;
  height: 99%;
  background-size: cover;
  background-position: center;
  filter: grayscale(100%);
  opacity: 0;
  transition: opacity 0.5s ease-in-out, filter 0.5s ease;
}

/* Content (Heading) */
.tl-content {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  padding: 15px;
  width: 80%;
  border-radius: 10px;
}

.tl-content p {
  font-size: 1rem;
  font-weight: var(--font-r);
  color: #FEFEFE;
  line-height: 1.3125rem;
  text-transform: uppercase;
  margin: 0;
}

/* Make the first image and content visible by default with full width */
.tl-item:first-child {
  width: 73%;
}

.tl-item:first-child .tl-bg,
.tl-item:first-child .tl-content {
  opacity: 1;
  filter: grayscale(0);
}
.tl-item:first-child .tl-bg img,
.tl-item:first-child .tl-content img {
  width: 100%;
}

/* Hide the first image and content when any other item is hovered */
#timeline:hover .tl-item:first-child .tl-bg,
#timeline:hover .tl-item:first-child .tl-content {
  opacity: 0;
}

#timeline:hover .tl-item:first-child {
  width: 10%;
}

/* Hover Effects */
.tl-item:hover .tl-year {
  opacity: 0;
}

.tl-item:hover .tl-bg {
  opacity: 1;
  filter: grayscale(0);
}
.tl-item:hover .tl-bg img {
  width: 100%;
}

.tl-item:hover .tl-content {
  opacity: 1;
}

.tl-item:hover {
  width: 73%;
}

